body {
  margin: 0 0;
  padding: 0 0;
  font-family: "Raleway";
  color: #a8a7a7;
}

h1 {
  font-size: 60px;
  font-weight: 700;
}

h2 {
  font-size: 36px;
  font-weight: 700;
  color: #414042;
  position: relative;
}

h2::after {
  position: absolute;
  content: "";
  border-top: 1px solid #c1c1c1;
  width: 300px;
  top: 150%;
  left: 50%;
  transform: translate(-50%);
}

span {
  color: #1ac9c7;
}

.container {
  margin-left: 100px;
  margin-right: 100px;
}

@media (max-width: 750px) {
  .container {
    margin-left: 20px;
    margin-right: 20px;
  }
}

.row [class^="col-"] {
  padding: 20px 10px 10px 10px;
  margin-top: 10px;
  margin-bottom: 10px;
}

@media (min-width: 975px) {
  .row {
    display: flex;
  }
  .row .col-1 {
    width: 8.33333%;
  }
  .row .col-2 {
    width: 16.66667%;
  }
  .row .col-3 {
    width: 25%;
  }
  .row .col-4 {
    width: 33.33333%;
  }
  .row .col-5 {
    width: 41.66667%;
  }
  .row .col-6 {
    width: 50%;
  }
  .row .col-7 {
    width: 58.33333%;
  }
  .row .col-8 {
    width: 66.66667%;
  }
  .row .col-9 {
    width: 75%;
  }
  .row .col-10 {
    width: 83.33333%;
  }
  .row .col-11 {
    width: 91.66667%;
  }
  .row .col-12 {
    width: 100%;
  }
}

.page__header {
  display: flex;
  box-sizing: border-box;
  height: 70px;
  justify-content: space-around;
  align-items: center;
  border-top: 5px solid #1ac9c7;
}

.navigation .navigation__list {
  list-style: none;
  display: flex;
}

@media (max-width: 700px) {
  .navigation .navigation__list {
    flex-direction: column;
  }
}

.navigation .navigation__list .navigation__item a {
  text-decoration: none;
  color: #a8a7a7;
  padding: 30px;
  transition: color 0.1s 0.1s ease-in-out;
}

@media (max-width: 850px) {
  .navigation .navigation__list .navigation__item a {
    padding: 30px 15px;
  }
}

.navigation .navigation__list .navigation__item a:hover {
  color: #1ac9c7;
}

.jumbotron {
  min-height: calc(100vh - 70px);
  background: no-repeat url("../../assets/background.jpg");
  background-position: center;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
  color: #fff;
}

.jumbotron::after {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.jumbotron .jumbotron__header {
  margin-bottom: -35px;
  z-index: 2;
}

.jumbotron .jumbotron__description {
  font-size: 36px;
  font-weight: 300;
  line-height: 60px;
  z-index: 2;
}

.jumbotron .jumbotron__button {
  position: relative;
  z-index: 2;
  width: 230px;
  height: 50px;
  box-sizing: border-box;
  background-color: rgba(255, 255, 255, 0.25);
  border: none;
  border-radius: 10px;
  color: #fff;
  transition: background-color 0.1s 0.1s ease-in-out;
  cursor: pointer;
  border: 1px solid #fafafa;
  margin: 0 0.5rem 4rem 0.5rem;
  transition: background-color 0.2s 0.1s ease-in-out, border-color 0.2s 0.1s ease-in-out;
}

.jumbotron .jumbotron__button:hover {
  background-color: rgba(204, 204, 204, 0.25);
}

.jumbotron .jumbotron__button:hover {
  background-color: #1ac9c7;
  border-color: #1ac9c7;
}

.jumbotron .jumbotron__text {
  z-index: 2;
  opacity: 0.8;
}

.jumbotron .jumbotron__text::before, .jumbotron .jumbotron__text::after {
  display: inline-block;
  content: "";
  border-top: 2px solid #fff;
  width: 300px;
  margin: 0 2rem;
  transform: translateY(-0.25rem);
}

.jumbotron .jumbotron__partners {
  z-index: 2;
  opacity: 0.8;
  position: relative;
}

.jumbotron .jumbotron__partners::before, .jumbotron .jumbotron__partners::after {
  display: inline-block;
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  border-bottom: 1.5px solid #fff;
  border-right: 1.5px solid #fff;
  top: 150%;
  left: 50%;
  transform: rotate(45deg) translate(-70%);
}

.jumbotron .jumbotron__partners::after {
  width: 7px;
  height: 7px;
  top: 180%;
}

.how-to {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

.how-to .how-to__item {
  margin: 0 20px;
  border-radius: 10px;
  padding: 10px 25px 5px 25px;
  transition: color 0.2s 0.2s ease-in-out;
}

.how-to .how-to__item:hover {
  color: #1ac9c7;
}

.how-to .how-to__item .item__name {
  font-size: 20px;
}

.how-to .how-to__item .item__name::after {
  display: inline-block;
  content: "";
  border-top: 1px solid #c1c1c1;
  width: 200px;
  margin: 0 2rem;
  transform: translateY(-0.25rem);
}

.how-to .how-to__button {
  width: 230px;
  height: 50px;
  box-sizing: border-box;
  background-color: #1ac9c7;
  border: none;
  border-radius: 10px;
  color: #fff;
  transition: background-color 0.1s 0.1s ease-in-out;
  cursor: pointer;
  border-bottom: 3px solid #0e6f6e;
  margin: 70px;
}

.how-to .how-to__button:hover {
  background-color: #0e6f6e;
}

.testimonials {
  background-color: #f4f4f4;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

.testimonials .testimonials__description {
  margin-bottom: 80px;
}

.testimonials .testimonials__user {
  transition: color 0.2s 0.2s ease-in-out;
}

.testimonials .testimonials__user:hover {
  color: #1ac9c7;
}

.testimonials .testimonials__user .user__avatar img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 5px solid #e4e5e5;
  object-fit: cover;
  object-position: center;
}

.testimonials .testimonials__user .user__name {
  font-size: 20px;
  margin: 0;
}

.testimonials .testimonials__user .user__company {
  font-weight: 300;
  margin-top: 0;
  position: relative;
}

.testimonials .testimonials__user .user__company::after {
  position: absolute;
  content: "";
  border-top: 1px solid #dbdada;
  width: 200px;
  top: 150%;
  left: 50%;
  transform: translate(-50%);
  margin-top: 5px;
}

.testimonials .testimonials__user .user__opinion {
  margin-top: 30px;
  font-style: italic;
  font-size: 14px;
}

.why {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

.why .reason__item {
  border: 1px solid #e4e5e5;
  margin: 15px;
  padding: 30px 25px;
  transition: color 0.2s 0.2s ease-in-out, border 0.2s 0.2s ease-in-out;
  box-sizing: border-box;
}

.why .reason__item:hover {
  color: #1ac9c7;
  border: 1px solid #1ac9c7;
}

.why .reason__item:hover .reason__name {
  color: #1ac9c7;
}

.why .reason__item .reason__name {
  font-size: 30px;
  font-weight: 700;
  color: #414042;
  transition: color 0.2s 0.2s ease-in-out;
}

.why .why__button {
  width: 230px;
  height: 50px;
  box-sizing: border-box;
  background-color: #1ac9c7;
  border: none;
  border-radius: 10px;
  color: #fff;
  transition: background-color 0.1s 0.1s ease-in-out;
  cursor: pointer;
  border-bottom: 3px solid #0e6f6e;
  margin: 30px;
}

.why .why__button:hover {
  background-color: #0e6f6e;
}

.video {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.video .video__navigation {
  list-style: none;
  display: flex;
}

.video .video__navigation .video__navigation-item a {
  text-decoration: none;
  color: #b6b5b6;
  padding: 0 30px;
  transition: color 0.2s 0.2s ease-in-out;
}

.video .video__navigation .video__navigation-item a:hover {
  color: #1ac9c7;
}

.video .video__button {
  width: 230px;
  height: 50px;
  box-sizing: border-box;
  background-color: #1ac9c7;
  border: none;
  border-radius: 10px;
  color: #fff;
  transition: background-color 0.1s 0.1s ease-in-out;
  cursor: pointer;
  border-bottom: 3px solid #0e6f6e;
  margin: 50px;
}

.video .video__button:hover {
  background-color: #0e6f6e;
}

.video__item {
  background-repeat: no-repeat;
  background-size: cover;
  background-position-y: -50px;
  background-color: #414042;
  border-radius: 10px;
  padding: 150px;
  margin: 15px;
  color: #fff;
  text-align: center;
  position: relative;
  transition: 0.5s 0s ease-in-out;
  cursor: pointer;
}

.video__item:hover {
  background-color: #1ac9c7;
}

.video__item:hover::after {
  animation: opacity-change 0.5s 0s linear forwards;
}

.video__item:hover::before {
  animation: opacity-change 0.5s 0s linear forwards;
}

.video__item::before {
  content: url("../../assets/icons/play.svg");
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  opacity: 0;
  position: absolute;
}

.video__item::after {
  content: "";
  width: 100%;
  height: 83.5%;
  background-color: rgba(0, 0, 0, 0.5);
  background-position-y: -50px;
  top: 0;
  left: 0;
  position: absolute;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  opacity: 0;
}

.video__item:nth-of-type(1) {
  background-image: url("../../assets/bar.jpeg");
}

.video__item:nth-of-type(2) {
  background-image: url("../../assets/scooter.jpg");
}

.video__item:nth-of-type(3) {
  background-image: url("../../assets/smartphone.jpg");
}

.video__item:nth-of-type(4) {
  background-image: url("../../assets/party.jpg");
}

.video__item .video__name {
  position: absolute;
  top: 83%;
  left: 50%;
  transform: translate(-50%);
}

@keyframes opacity-change {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
